.container-fluid{
    background-color: rgb(14, 18, 53);
    margin-top: -10px;
    margin-bottom: 10px;
    padding-top: 10px;
    padding-bottom: 10px;
    align-items: center;
}
.container-fluid h2{
    padding-left: 20px;
    width:75%;
    color: white;
    z-index: 100;
}
.nav-item{
    cursor: pointer;
    padding-top: -10px;
}
.navbar{
    z-index: 100;
}
@media only screen and (max-width: 480px) {
    .container-fluid h2{
        display: flex;
        flex-direction: row;
        justify-content: center;
        text-align: center;
        margin-left: 30px;
        padding-top: 5px;
    }
}